.castContainer {
  max-width: 1200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  padding: 16px;
}
.actorImg {
  overflow: hidden;
}
.actorImg > img {
  max-width: 240px;
  min-height: 375px;
  object-fit: cover;
}
.actorItem {
  display: flex;
  flex-direction: column;
  height: 450px;
  overflow: hidden;
}
.ActorInfo {
  flex-grow: 1;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
}
.actorName {
  font-size: 18px;
  padding: 4px;
}
.actorChar {
  max-width: 240px;
  font-size: 14px;
  color: orange;
  padding: 4px;
  word-wrap: break-word;
}
